@import url(https://fonts.googleapis.com/css2?family=Harmattan:wght@400;700&display=swap);
:root {
  --bg-color: #212529;
  --secondary-color: #343a40;
  --heading-color: #f8f9fa;
  --font-color: #dee2e6;
  --action-color: #ff9e00;
}

[data-theme = "light"] {
  --bg-color: #f8f9fa;
  --secondary-color: #dee2e6;
  --heading-color: #212529;
  --font-color: #343a40;
  --action-color: #ff9e00;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: #212529;

  background-color: var(--bg-color);
  color: #dee2e6;
  color: var(--font-color);
  font-family: 'Harmattan', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



nav {
	background-color: var(--secondary-color);

	display: flex;
	flex-wrap: nowrap;
	justify-content: space-around;

	position: -webkit-sticky;

	position: sticky;
	top: 0;
	z-index: 3000;

	padding: 1% 0;
}

nav h2 {
	margin: 0;
	font-size: 2rem;
	font-weight: 700;
	color: var(--action-color);
}

.navlinks {
	width: 40%;

	display: flex;
	flex-wrap: nowrap;
	justify-content: space-around;
}

.navlinks a {
	font-size: 1.7rem;
	font-weight: 600;
	text-decoration: none;
	color: var(--font-color);
}

.navlinks a:hover {
	color: var(--heading-color);
}

.navlinks a:active,
.navlinks a.active {
	color: var(--action-color);
}

.actions svg:hover {
	color: var(--heading-color);
	cursor: pointer;
}

.action-btn {
	background-color: transparent;
	border: none;
	color: var(--font-color);
	padding-top: 15%;
}

.homediv {
    padding: 6% 5%;
}

form {
    display: flex;
    align-items: center;
    justify-content: center;
    
    margin: 0 0 6% 0;
}

.homediv form input {
    width: 50%;
    padding: 1% 3%;
    background-color: transparent;
    border: 2px solid var(--font-color);
    border-radius: 15px;
    color: var(--font-color);
    font-size: 1.1rem;
}

.homediv form input:focus {
    border: 2px solid var(--heading-color);
}

.homediv form button {
    margin-left: 2%;
    background-color: transparent;
    border: 2px solid var(--font-color);
    border-radius: 15px;
    padding: .9% 1.5%;
    color: var(--font-color);
}

.content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

/* spping the circle */
.loader {
    border: 3px solid transparent;
    border-radius: 50%;
    border-top: 3px solid var(--action-color);
    width: 50px;
    height: 50px; /* Safari */
    animation: spin 1s linear infinite;
  }
  
  /* Safari */
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }


  /* ============ for mobile version ui ============ */

@media (min-width: 320px) and  (max-width: 425px) {
    .homediv {
        padding: 1% 4%;
    }
}
.carddiv {
    width: 18%;
    margin: 1%;
    padding: 1.5% 1.3%;
    background-color: var(--secondary-color);
    color: var(--font-color);

    border-radius: 12px;
}

.carddiv img {
    width: 100%;
    height: auto;
    border-radius: 12px;

    margin-bottom: 5%;
}

.carddiv p {
    font-size: 1.1rem;
    line-height: 1;
    opacity: 0.8;
}

.carddiv a {
    color: var(--heading-color);
    text-decoration: none;
    font-size: 1.5rem;
    font-weight: 600;

    line-height: 1;
}

.carddiv a:hover {
    color: var(--action-color);
}

.card-action-div {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: nowrap;

    margin-top: 7%;
}

.card-action-div b {
    font-size: 1.3rem;
    font-weight: 700;
}

.card-watch-btn {
    font-size: 0.4rem;
    font-weight: 500;
    color: var(--bg-color) !important;
    background-color: var(--action-color);
    padding: 4% 5%;
    border-radius: 6px;
}

.card-watch-btn:hover {
    color: var(--bg-color);
    background-color: #ff7900;
    cursor: f;
}

/* =========== for tablet version ui========== */
@media (min-width: 768px) and (max-width: 1024px) {
    .carddiv {
        width: 30%;
    }
}

/* ============ for mobile version ui ============ */

@media (min-width: 320px) and  (max-width: 425px) {
    .carddiv {
        width: 45%;
        padding: 3.1% 3%;
    }
}
.top-div {
	padding: 6% 5%;
}

.articlediv {
    padding: 6% 10%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.poster-div img {
    border-radius: 12px;
}

.info-div h2 {
    color: var(--action-color);
    font-size: 2.5rem;
    font-weight: 600;
    margin: 0;
}

.info-div p {
    font-size: 1.4rem;
    line-height: 1;
    font-weight: 400;
    color: var(--font-color);
    margin: 2% 0;
}

.info-div span {
    font-size: 1.3rem;
}

#moreinfo {
    width: 100%;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-around;
}

.watch-btn {
    text-decoration: none;
    background-color: var(--action-color);
    padding: 1% 4%;
    color: var(--bg-color);
    font-size: 1.3rem;
    font-weight: 600;
    border-radius: 12px;
}

.watch-btn:hover {
    color: var(--bg-color);
    background-color: #ff7900;
}
